                          v-model.number="Items.occupy.occupy14.percentage"
                          :disabled="Items.occupy.occupy14.disabled"
                          :rules="Items.occupyRules.occupy14"
                          :label="$ln(Items.occupy.occupy14.title)"
                          :suffix="Items.occupy.occupy14.suffix"
                          class="v-input-input"
                        />
                      </v-flex>
                      <v-flex
                        v-if="Items.occupy.occupy15.show"
                        md2
                        xs4>
                        <v-text-field
                          v-model.number="Items.occupy.occupy15.percentage"
                          :disabled="Items.occupy.occupy15.disabled"
                          :rules="Items.occupyRules.occupy15"
                          :label="$ln(Items.occupy.occupy15.title)"
                          :suffix="Items.occupy.occupy15.suffix"
                          class="v-input-input"
                        />
                      </v-flex>
                      <v-flex
                        v-if="Items.occupy.bingo.show"
                        md2
                        xs4>
                        <v-text-field
                          v-if="!Items.occupy.bingo.disabled"
                          v-model.number="Items.occupy.bingo.percentage"
                          :disabled="Items.occupy.bingo.disabled"
                          :rules="Items.occupyRules.bingo"
                          :label="$ln(Items.occupy.bingo.title)"
                          :suffix="Items.occupy.bingo.suffix"
                          class="v-input-input"
                        />
                        <v-text-field
                          v-else
                          v-model.number="Items.occupy.bingo.percentage"
                          :disabled="Items.occupy.bingo.disabled"
                          :label="$ln(Items.occupy.bingo.title)"
                          :suffix="Items.occupy.bingo.suffix"
                          class="v-input-input"
                        />
                      </v-flex>
                      <v-flex
                        v-if="Items.occupy.royal.show"
                        md2
                        xs4>
                        <v-text-field
                          v-if="!Items.occupy.royal.disabled"
                          v-model.number="Items.occupy.royal.percentage"
                          :disabled="Items.occupy.royal.disabled"
                          :rules="Items.occupyRules.royal"
                          :label="$ln(Items.occupy.royal.title)"
                          :suffix="Items.occupy.royal.suffix"
                          class="v-input-input"
                        />
                        <v-text-field
                          v-else
                          v-model.number="Items.occupy.royal.percentage"
                          :disabled="Items.occupy.royal.disabled"
                          :label="$ln(Items.occupy.royal.title)"
                          :suffix="Items.occupy.royal.suffix"
                          class="v-input-input"
                        />
                      </v-flex>
                    </v-layout>
                  </div>
                </div>
              </v-flex>
            </v-layout>
          </v-form>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="blue darken-1"
          flat
          @click="Items.dialogOpen = false">{{ $ln('取消') }}</v-btn>
        <v-btn
          v-if="Items.valid && Items.account.selected"
          color="deep-orange darken-1"
          @click="add()">{{ $ln('新增') }}</v-btn>
        <v-btn
          v-else
          disabled
          color="deep-orange darken-1">{{ $ln('新增') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Append  from '@/mixins/account-management/level-management/Append.vue'

export default {
  name: 'AppendSuperAccount',
  mixins:[ Append ] ,
  props: {
    upAccount: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      levelName: '大股東',
      // 大股東層級代號
      level: 5,
      // 上層(總監)層級代號
      upLevel: 6,
      // 路徑
      path:''
    }
  },
  computed: {
  },
  watch: {
    getGenerateGleadAccountState (payload) {
      this.Items.account.selected = payload.result.account[0]
      this.Items.account.options = payload.result.account
    },
    // 依照「開放BingoStar」狀態 disable / enabled 額度輸入欄及拆帳百分比
    'Items.openBingo.selected' (status) {
      this.Items.bingoCreditItems.disabled = status === 1
      this.Items.bingoCashItems.disabled = status === 1
      this.Items.occupy.bingo.disabled = status === 1
    },
    // 依照「開放真人(皇家)類別」狀態 disable / enabled 額度輸入欄及拆帳百分比
    'Items.openRoyal.selected' (status) {
      this.Items.royalCreditItems.disabled = status === 1
      this.Items.royalCashItems.disabled = status === 1
      this.Items.occupy.royal.disabled = status === 1
    },
    'Items.dialogOpen' (dialogOpen) {
      // 關閉新增帳號彈窗
      if (!dialogOpen) {
        // 清除欄位資料
        this.clearField()
        // 關閉彈窗 reset 驗證功能
        this.$refs.form.resetValidation()

        /* 重新刷新資料 */
        // 取得自動生成帳號編號(管端帳號)
        this.loadGenerateAccount()
        // 取得預設資料
        this.getDefault()
        // 載入[BingoStar]上層帳號資訊
        this.loadUpAccountBingoStar()
        // 載入[Super彩球]上層帳號資訊
        this.loadUpAccountSuperLottery()
        // 載入[皇家]上層帳號資訊
        this.loadUpAccountRoyal()
        // 載入上層帳號資訊
        this.loadUpAccount()
      }
    }
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  mounted () {
    // 路徑
    let temp = (this.$route.path).split('/')
    this.path = `/manage/${temp[2]}/${temp[3]}/`

    // 取得自動生成帳號編號(管端帳號)
    this.loadGenerateAccount()
    // 取得預設資料
    this.getDefault()
    // 載入[BingoStar]上層帳號資訊
    this.loadUpAccountBingoStar()
    // 載入[Super彩球]上層帳號資訊
    this.loadUpAccountSuperLottery()
    // 載入[皇家]上層帳號資訊
    this.loadUpAccountRoyal()
    // 載入上層帳號資訊
    this.loadUpAccount()

    // 依照「開放BingoStar」狀態 disable / enabled 額度輸入欄
    this.Items.bingoCreditItems.disabled = this.Items.openBingo.selected === 1
    this.Items.bingoCashItems.disabled = this.Items.openBingo.selected === 1

    // 依照「開放真人(皇家)類別」狀態 disable / enabled 額度輸入欄
    this.Items.royalCreditItems.disabled = this.Items.openRoyal.selected === 1
    this.Items.royalCashItems.disabled = this.Items.openRoyal.selected === 1

    this.updateDashboardInfo()
  }
}
</script>

